.editForm {
    text-align: left;
    width: 320px;
    padding: 16px;
    border: 1px solid #212121;
    border-radius: 4px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.8);
    background-color: white;
}

.editInput {
    display: flex;
    align-items: center;
    margin-left: 8px;
    padding: 4px;
}

.saveButton {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    padding: 0;
    border: none;
    background-color: transparent;
    border-radius: 50%;
    cursor: pointer;

    transition: color 250ms linear, background-color 250ms linear;
}

.saveButton:hover {
    background-color: #ffcc00;
    color: white;
}

.mistake {
    position: absolute;
    bottom: 0;
    transform: translatey(100%);

    font-size: 10px;
    background-color: white;
    color: red;
}